.dep-container {
  background-color: var(--color-2);
}
.departure {
  background-color: var(--color-2);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1em 0;
  flex-shrink: 0;
  align-items: stretch;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  z-index: 1;
}
.departure-link {
  margin: 0.2em;
}
